.a{
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-right{
    flex: 1;
}

.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: greenyellow;
    overflow: hidden;
}

.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
}

.a-img{
    width: 100% ;
    height: 100%;
    border-radius: 30px;
    object-fit: cover;
    background-size: cover;
}

.a-award-img{
    width: 300px;
    height: 360px;
    border-radius: 20px;
}

.a-title{
    font-weight: 400;
}

.a-sub{
    margin: 20px 0;
}

.a-desc{
    font-weight: 300;
}

.a-award{
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.a-award-texts{
    width: 70%;
    margin: 0 30px;
}

.a-award-title{
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}

@media screen and (max-width:480px) {
    .a{
      flex-direction: column;
      text-align: center;
      padding: 50px 0;
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 30vh;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
}